@font-face {
  font-family: Poppins;
  font-display: swap;
  src: url("Poppins.dbaa922b.woff2");
}

@font-face {
  font-family: Digital-7;
  font-display: swap;
  src: url("Digital-7.de6bf173.woff2");
}

:root {
  --shadow: #1f1f1f;
  --white: #fbfbf8;
  --mint: #d3eedd;
  --tomato: #ff3814;
  --dark-red: #cc2c10;
  --green: #57b752;
  --dark-green: #105010;
  scroll-behavior: smooth;
  background-size: contain;
  background-color: var(--mint);
  background-image: url("background.e717fc6f.webp");
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

body {
  display: inherit;
  user-select: none;
  text-align: center;
  text-shadow: 2px 2px 1px var(--shadow);
  flex-direction: column;
  gap: .5rem;
  font-family: Poppins, sans-serif;
}

header {
  color: var(--green);
  height: 3rem;
  line-height: 0;
}

main {
  color: var(--white);
  background: var(--tomato);
  box-shadow: inset -1rem .25rem .75rem .25rem tomato, 0 0 .25rem 1px var(--shadow);
  border-radius: 9rem;
  grid-template-columns: 1fr;
  width: 18rem;
  height: 17rem;
  margin: 0 1rem;
  padding: 1rem;
  display: grid;
}

figure {
  margin-top: 1rem;
  margin-bottom: -1rem;
  font-size: 1rem;
  font-weight: bold;
  line-height: 0;
}

span:not(#time-label) {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: .75rem;
  font-weight: normal;
  display: flex;
}

button {
  display: inherit;
  cursor: pointer;
  background: none;
  border: 0;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
}

button:active {
  background: var(--dark-red);
}

button:disabled {
  cursor: not-allowed;
  background: none;
}

i {
  color: var(--white);
  transform: scale(.875);
}

.increment {
  transform: rotate(180deg);
}

p, time {
  width: 1.6rem;
  font-family: Digital-7, monospace;
  font-size: 27px;
}

section {
  text-align: center;
  grid-column: 1;
}

h2 {
  font-size: 19px;
  line-height: 1rem;
}

time {
  font-size: 2rem;
}

footer {
  color: var(--dark-red);
  text-shadow: 2px 0 1px var(--white);
  font-size: 17px;
  font-weight: bold;
}

a {
  color: var(--shadow);
  font-weight: normal;
}

a:hover {
  color: var(--dark-green);
}

@media (width >= 36rem) {
  main {
    border-radius: 13rem;
    grid-template-columns: 1fr 1fr;
    width: 32rem;
    height: 24rem;
  }

  figure {
    margin-top: 4rem;
    margin-bottom: 0;
    font-size: 20px;
  }

  span:not(#time-label) {
    margin-top: 1rem;
  }

  button {
    width: 2.25rem;
    height: 2.25rem;
  }

  i {
    transform: scale(1);
  }

  p, time {
    font-size: 2rem;
  }

  time {
    font-size: 3rem;
  }

  section {
    grid-column: 1 / span 2;
  }

  h2 {
    font-size: 1.5rem;
  }
}

@media (height <= 36rem) {
  :root {
    padding: 6rem;
  }
}
